<template>
  <div>
    <v-phone :list="list" style="transform: scale(0.8)"></v-phone>
  </div>
</template>

<script>
  import VPhone from 'vue-phone-in-browser'
  import 'vue-phone-in-browser/dist/phone.css';
  import icon from '../assets/logo.png'
  export default {
    name: "Demos",
    components: {VPhone},
    data() {
      return {
        // phoneType: 'i5',//手机的大小可以选择：i6,i7,i8,i6s,i7s,i8s,ipad,ipad-pro，默认为iphone5的尺寸。
        list: [{
          title: 'reader', //设置桌面主题title
          icon: icon,//设置一个桌面图标
          url: 'https://jiangmengxia.github.io/uniapp-hjfhkshdflsjlfkjn779u/reader',//需要打开的链接
        }]
      }
    }
  }
</script>

<style scoped>

</style>



